@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 75px;
}

.page {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  overflow-x: hidden;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  font-family: "Roboto", sans-serif;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  color: #ffffff;
  background-color: #000000;
}

.header {
  padding: 0 69px 0 72px;
}

.header-intro {
  position: relative;
  z-index: 1;
  min-height: 852px;
  background-repeat: no-repeat;
  background-position: right;
}
.header-intro::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 30%;
  z-index: -1;
  background-image: url("../img/background/header-background.png");
  background-repeat: no-repeat;
  background-size: cover;
}
.header-intro::after {
  position: absolute;
  z-index: 2;
  content: "";
  bottom: 0;
  top: 850px;
  right: 0;
  height: 100px;
  background-color: #000000;
  width: 600px;
  box-shadow: 0 0 65px 75px #000000;
}

.header__nav {
  display: flex;
  align-items: center;
  justify-content: start;
  padding: 53px 0;
}

.header__info {
  margin-top: 210px;
}

.header__title {
  font-weight: 700;
  font-size: 110px;
  line-height: 110%;
  margin: 0 0 10px;
  padding: 0;
  text-transform: uppercase;
  letter-spacing: -3px;
}

.header__description {
  font-size: 22px;
  line-height: 26px;
  margin: 0 0 6px;
  padding: 0;
}

.header__genre {
  margin: 0 0 30px;
  padding: 0;
}
.header__genre span {
  text-transform: uppercase;
}

.header__link-marvel {
  margin: 0;
  padding: 0;
}

.header__marvel-image {
  display: block;
  margin: -60px 0;
  transition-property: transform;
  transition-duration: 300ms;
}
.header__marvel-image:hover {
  transform: scale(1.1);
}
.header__marvel-image:active {
  opacity: 0.5;
}

.header__button-container {
  width: max-content;
  background-color: #ec1d24;
  border: none;
}

.header__button {
  font-weight: 500;
  color: #ffffff;
  text-decoration: none;
  border: none;
}

.header__block {
  display: flex;
  align-items: center;
}

.header__rating {
  margin-left: 18px;
}

.button {
  margin: 0;
  padding: 22px 45px;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  transition-duration: 300ms;
  transition-property: transform;
}
.button:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.button:active {
  opacity: 0.5;
}

.navigation {
  display: flex;
  flex-wrap: wrap;
  margin-left: 14%;
  background-color: transparent;
}
.navigation--footer {
  margin-left: 21%;
}

.navigation__list {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.navigation__item {
  transition-duration: 300ms;
  transition-property: transform;
}
.navigation__item:hover {
  transform: scale(1.2);
}
.navigation__item:active {
  opacity: 0.5;
}

.navigation__link {
  margin: 0;
  padding: 15px 14px;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
}

.navigation__link--subscribe {
  position: relative;
  min-width: 166px;
  margin-left: auto;
  padding-right: 0;
  transition-duration: 300ms;
  transition-property: transform;
}
.navigation__link--subscribe:hover {
  transform: scale(1.2);
}
.navigation__link--subscribe:active {
  opacity: 0.5;
}
.navigation__link--subscribe::before {
  content: "";
  position: absolute;
  bottom: 15px;
  left: 15px;
  width: calc(100% - 15px);
  height: 1px;
  background-color: #ffffff;
}

.navigation__footer {
  background-color: #000000;
}

.information {
  padding-top: 140px;
}

.information__title {
  width: calc(50% - 30px);
  margin: 0 0 104px;
  padding: 0 0 20px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
  border-bottom: 1px solid #ffffff;
}

.information__description {
  display: grid;
  gap: 30px;
  grid-template-columns: 1fr 1fr;
}

.information__description-title {
  max-width: 35%;
  font-weight: 700;
  font-size: 62px;
  line-height: 100%;
  color: #ec1d24;
  text-transform: uppercase;
}

.information__description-block {
  display: flex;
}

.information__description-text {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
}

.button__information {
  position: relative;
  width: 93px;
  height: 97px;
  margin-left: 85px;
  background-image: url("../img/background/trailer.svg");
  background-repeat: no-repeat;
  background-size: 93px 97px;
  transition-duration: 500ms;
}
.button__information::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 55%;
  width: 19px;
  height: 23px;
  background-image: url("../img/background/polygon.svg");
  background-repeat: no-repeat;
  background-position: center;
  transform: translate(-50%, -50%);
}
.button__information:hover {
  transform: rotate(360deg) scale(1.4);
  cursor: pointer;
}
.button__information:active {
  opacity: 0.5;
}

.information__people {
  margin-top: 109px;
}

.information__people-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 0 83px;
  padding: 0;
  list-style-type: none;
}

.information__people-item {
  font-weight: 400;
  font-size: 22px;
  line-height: 26px;
  transition-duration: 300ms;
  transition-property: transform;
}
.information__people-item:hover {
  transform: scale(1.2);
  cursor: pointer;
}
.information__people-item:active {
  opacity: 0.5;
}
.information__people-item--current:hover {
  transform: none;
  cursor: default;
}
.information__people-item--current:active {
  cursor: default;
  opacity: 1;
}

.information__people-link {
  padding: 15px;
  color: #ffffff;
  text-decoration: none;
}
.information__people-link:first-of-type {
  padding-left: 0;
}
.information__people-link:last-of-type {
  padding-right: 0;
}
.information__people-link--current {
  color: #ec1d24;
  text-decoration: underline;
}
.information__people-link--current:hover {
  cursor: default;
}

.information__producer-list {
  display: grid;
  margin: 0 0 150px;
  padding: 0;
  list-style-type: none;
  gap: 30px;
  grid-template-columns: repeat(4, 1fr);
}

.information__producer-image {
  margin-bottom: 20px;
}

.information__producer-name {
  text-transform: uppercase;
}

.information__history {
  display: flex;
  align-items: center;
  margin-top: -50px;
}

.information__history-image {
  display: block;
  max-width: none;
  margin-right: -75px;
  object-fit: cover;
}

.information__history-container {
  position: relative;
}
.information__history-container::before {
  content: "";
  position: absolute;
  position: absolute;
  top: 60%;
  left: 75%;
  z-index: -1;
  width: 1px;
  height: 100px;
  background-color: #ec1d24;
  border-radius: 50%;
  box-shadow: 0 0 360px 269px #ec1d24;
}

.information__history-description {
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 62px;
  line-height: 120%;
  text-transform: uppercase;
  letter-spacing: -3px;
}

.information__history-special {
  color: #ec1d24;
  text-decoration-line: underline;
}

.information__image-containe {
  position: relative;
}
.information__image-containe::before {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 348px;
  background-image: url("../img/background/mask.png");
  background-size: cover;
}

.information__producer-image {
  z-index: -1;
  display: block;
  object-fit: cover;
}

.actor {
  padding-top: 122px;
  padding-bottom: 121px;
  background-color: #ec1d24;
}

.actor__title {
  margin: 0;
  padding: 0 0 20px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
  border-bottom: 1px solid #ffffff;
}

.actor__list {
  margin: 0;
  padding: 0;
}

.actor__item {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  color: #ffffff;
  text-decoration: none;
  border-bottom: 1px solid #ffffff;
  transition-duration: 300ms;
  transition-property: transform;
}
.actor__item:hover:not(.actor__item--current) {
  transform: scale(1.03);
}

.actor__item--current {
  position: relative;
  z-index: 1;
  color: #ec1d24;
}
.actor__item--current:hover {
  cursor: default;
}
.actor__item--current::before {
  content: "";
  position: absolute;
  top: -1px;
  left: -100%;
  z-index: -1;
  width: 1000%;
  height: calc(100% + 3px);
  background-color: #ffffff;
}
.actor__item--current::after {
  content: "";
  position: absolute;
  top: -175%;
  left: 26%;
  z-index: 1;
  width: 300px;
  height: 420px;
  background-image: url("../img/background/actor-harbour.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.actor__name {
  padding: 32px 10px 28px 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  text-transform: uppercase;
}

.actor__character {
  width: max-content;
  min-width: 250px;
  font-weight: 500;
  font-size: 22px;
  line-height: 26px;
  text-align: start;
  text-transform: uppercase;
}

.review {
  position: relative;
  padding-top: 120px;
}
.review::before {
  content: "";
  position: absolute;
  bottom: -1px;
  left: -100%;
  z-index: 1;
  width: 1000%;
  height: 1%;
  background-color: #ec1d24;
}

.review__title-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 1px solid #ffffff;
}

.review__title {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
}

.review__counter span {
  color: rgba(255, 255, 255, 0.5);
}

.review__list {
  display: grid;
  margin: 0;
  margin-bottom: 120px;
  padding: 0;
  list-style-type: none;
  gap: 30px;
  grid-template-columns: 1fr 1fr;
}

.review__item {
  display: grid;
  gap: 45px;
  grid-template-columns: 2fr 1.9fr;
}

.review__people {
  display: flex;
  flex-direction: column;
}

.review__name {
  margin-bottom: 5px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
}

.review__job {
  font-size: 14px;
  line-height: 16px;
}

.review__info {
  display: flex;
  flex-direction: column;
  margin-top: 37px;
}

.review__description {
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
}

.review__link {
  margin: 0;
  padding: 10px 0;
  text-align: start;
  color: #ec1d24;
  text-transform: none;
  text-decoration: underline;
  border: none;
}

.review__button-container {
  display: flex;
  justify-content: end;
  align-items: center;
  margin: 30px auto;
}

.review__button {
  width: 45px;
  height: 45px;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: 1px solid #ffffff;
}
.review__button--back, .review__button--forward {
  position: relative;
}
.review__button--back::before, .review__button--forward::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 45px;
  height: 44px;
  background-image: url("../img/background/arrow.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.review__button--forward {
  margin-left: 10px;
}
.review__button--forward::before {
  transform: scale(-1);
}

.review__people-container {
  position: relative;
  margin-bottom: 20px;
}
.review__people-container::before {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 348px;
  background-image: url("../img/background/mask.png");
  background-size: cover;
}

.review__people-image {
  z-index: -1;
  display: block;
  object-fit: cover;
}

.advantages {
  display: grid;
  gap: 93px;
  grid-template-columns: 1fr 1fr;
}

.advantages__title {
  display: flex;
  justify-content: end;
  margin: 0 0 52px;
  padding: 0 0 20px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
  border-bottom: 1px solid #ffffff;
}

.advantages__info {
  margin-bottom: 52px;
  padding-bottom: 52px;
  border-bottom: 1px solid #ffffff;
}

.advantages__info-title {
  font-weight: 700;
  font-size: 62px;
  line-height: 73px;
  color: #ec1d24;
}

.advantages__description {
  width: 75%;
  margin: 15px 0 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
}

.advantages__price {
  margin: 0;
  margin-bottom: 56px;
  padding: 0;
  font-weight: 500;
  font-size: 22px;
  line-height: 26px;
}

.advantages__image-container {
  position: relative;
  order: -1;
  margin-top: auto;
}
.advantages__image-container::before {
  content: "";
  position: absolute;
  position: absolute;
  top: 47%;
  left: 46%;
  z-index: -1;
  width: 1px;
  height: 100px;
  background-color: #ec1d24;
  border-radius: 50%;
  box-shadow: 0 0 360px 269px #ec1d24;
}

.advantages__image {
  object-fit: cover;
}

.advantages__button-container {
  width: max-content;
  border: 1px solid #ffffff;
}

.advantages__button {
  margin: 0;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
}

.promotion {
  position: relative;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 257px;
  padding-bottom: 203px;
}
.promotion::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: -1;
  width: 1px;
  height: 1px;
  background-color: #ec1d24;
  border-radius: 50%;
  box-shadow: 0 0 270px 170px #ec1d24;
  transform: translate(-50%, -50%);
}

.promotion__title {
  margin: 0 auto 30px;
  padding: 0;
  font-weight: 700;
  font-size: 110px;
  line-height: 100%;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: -3px;
}

.promotion__logo {
  display: block;
  order: -1;
  margin-bottom: 15px;
}

.promotion__title-special {
  color: #ec1d24;
}

.promotion__image {
  position: absolute;
  top: 30%;
  right: 5%;
  z-index: -1;
}
.promotion__image--2 {
  top: 70%;
  right: 13%;
}
.promotion__image--3 {
  top: 70%;
  left: 18%;
}
.promotion__image--4 {
  top: 42%;
  left: 7%;
}
.promotion__image--5 {
  top: 14%;
  left: 28%;
}

.promotion__button {
  border: 1px solid #ffffff;
}

.advertising {
  position: relative;
  z-index: 2;
  padding: 116px 0;
  background-color: #ec1d24;
}

.advertising__title {
  margin: 0 0 60px;
  padding: 0 0 20px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
  border-bottom: 1px solid #ffffff;
}

.advertising__description {
  margin: 0 0 60px;
  padding: 0;
  font-weight: 700;
  font-size: 62px;
  line-height: 73px;
  text-transform: uppercase;
  letter-spacing: -3px;
}

.advertising__list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style-type: none;
  gap: 60px 30px;
  grid-template-columns: repeat(4, 1fr);
}

.advertising__item-endgame {
  grid-area: 1/1/2/3;
}

.advertising__item-ultron {
  grid-area: 2/3/3/5;
}

.advertising__image {
  display: block;
  transition-duration: 300ms;
  transition-property: transform;
}

.advertising__link {
  margin: 0;
  padding: 0;
  color: #ffffff;
  text-decoration: none;
}
.advertising__link:hover .advertising__image {
  transform: scale(1.05);
}

.advertising__item {
  position: relative;
}

.advertising__link-title {
  position: absolute;
  bottom: 30px;
  left: 15px;
  z-index: 1;
}

.footer {
  padding-bottom: 55px;
}

.footer__info {
  width: 30%;
  margin: 0;
  margin: 0 auto;
  padding: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 140%;
  text-align: center;
}

.contacts {
  padding-top: 28px;
  padding-bottom: 29px;
  background-color: #ec1d24;
}

.contacts__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.contacts__title {
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 62px;
  line-height: 73px;
  text-transform: uppercase;
  letter-spacing: -3px;
}

.contacts__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.contacts__item {
  position: relative;
  width: 47px;
  height: 47px;
  margin-right: 21px;
  border: 0.5px solid #ffffff;
  border-radius: 50%;
  transition-duration: 300ms;
  transition-property: transform;
}
.contacts__item:hover {
  transform: scale(1.2);
}
.contacts__item:active {
  opacity: 0.5;
}
.contacts__item:last-of-type {
  margin-right: 0;
}

.contacts__link {
  position: absolute;
  top: 55%;
  left: 50%;
  margin: 0;
  padding: 10px;
  transform: translate(-50%, -50%);
}